"use client";

import React from "react";
import { motion } from "framer-motion";

export const SocialIcon = ({ Icon, isActive, onClick }) => {
  const iconVariants = {
    initial: { scale: 1, color: "#6B7280" }, 
    active: { scale: 1.25, color: "primary" }, 
  };

  return (
    <motion.div
      initial="initial"
      animate={isActive ? "active" : "initial"}
      variants={iconVariants}
      whileTap={{ scale: 1.5 }}
      className="cursor-pointer"
      onClick={onClick}
    >
      <Icon />
    </motion.div>
  );
};

